.services {
  padding: 0 3rem 0 3rem;
  display: flex;
  width: 100%;
  height: 40rem;
  margin-bottom: 8rem;
  margin-top: 1rem;
  padding-top: 3rem;
}

.cards {
  position: relative;
}
.cards > * {
  position: absolute;
}

@media screen and (max-width: 480px) {
  .services {
    flex-direction: column;
    gap: 0rem;
    height: 62rem;
    padding: 0;
  }

  .cards {
    display: flex;
    flex-direction: column;
    gap: 20rem;
  }
  .cards > * {
    position: static;
  }
}


